import { Component } from '@angular/core';
import { APIService } from './api.service';
import { MaterialModule } from './material.module';
import { ResourceLoader } from '@angular/compiler';
import { Note } from './note';

interface User{
  name: String;
  id: number;
}


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  title = 'NotesApp';
  usersList: User[] = [];
  notes: Note[] = [];
  user: any;
  newUser: string;
  public makeNote : boolean;
  messageAddUser: any;
  errorMessage: string;
  messageObject: any;
  currentUser: any;
  newNote: string;
  editNote: boolean = false;
  currentNote: Note;


  constructor(public apiService: APIService) {
    apiService.getUsers().subscribe((data: User[]) =>{
      console.log(data);
      this.usersList = data;
    });
    this.makeNote=false;
  }

  GetNotes(userName){
    console.log(userName);
    if (userName != "undefined"){
      this.apiService.getNotesForUser(userName).subscribe((data: Note[]) => {
        this.notes= data;
      console.log(this.notes);
      this.currentUser= userName;
      });
    }else{
      this.DisplayMessage("Geef een naam in");
    }
  }

  DeleteNote(note: Note){
    console.log(note);
    this.apiService.DeleteNote(note.id).subscribe((response: any) => {
      console.log(response);
      this.GetNotes(this.currentUser);

    });
  }


  EditNote(note :Note){
    this.editNote= true;
    this.newNote= note.content;
    this.currentNote= note;
  }

  AddUser(userName){
    if (userName){
      this.apiService.AddUser(this.newUser).subscribe((response: any) => {
        console.log(response);
        this.messageAddUser = JSON.stringify(response);
        this.errorMessage = response.message;
        console.log(this.errorMessage);
        this.apiService.getUsers().subscribe((data: User []) =>{
          this.usersList= data;
        })
        if (this.errorMessage) this.DisplayMessage(this.errorMessage);
        this.newUser="";
        this.currentUser=this.newUser;
        this.GetNotes(userName);
      })
    } else{
      this.DisplayMessage("Geef een naam in");
    }
  }

  AddNote(currentUser, newNote){
    if (newNote){ // SAVE NEW NOTE
      if (!this.editNote){
        this.apiService.AddNote(this.currentUser, this.newNote)
          .subscribe((response: any) => {
            this.makeNote=true;
            console.log(response);
            this.newNote="";
        });
      }else{ // SAVE EDITED NOTE
        this.apiService.EditNote(this.currentNote.id, newNote).subscribe((data) => {
          console.log(data);
          this.apiService.getNotesForUser(this.currentUser).subscribe((data: Note[]) => {
            this.notes= data;
            console.log("we are in!", data);
            this.editNote= false;
            this.newNote= "";
          })
      });
    }
  }
}

DeleteUser(userName: string){
  if (userName) {
    this.apiService.DeleteUser(userName).subscribe((response: any) => {
        this.user=response;
        console.log(this.user);
        this.apiService.getUsers().subscribe((data: User []) =>{
          this.usersList= data;
        })
        console.log(response.success);
        if (response.success) this.DisplayMessage(response.success)
        else{
          window.alert(response.error)
        };
      })
  }
  if (this.currentUser == userName){
    this.currentUser="";
    this.notes=null;
  }
}

  DisplayMessage(message){
    window.alert(message)
  }


}
